<template>
    <div>
        <el-card class="box-card">
            <el-form
                ref="loginForm"
                :model="loginForm"
                :rules="loginFormRules"
            >
                <div style="text-align: center;">
                    <i class="icon iconfont icon-apphoutaiguanli" style="font-size: 100px; color: #E9232C;"></i>
                </div>
                <h2 style="text-align: center;">撩课-商城后台管理系统</h2>
                <el-form-item prop="username">
                    <el-input
                        v-model="loginForm.username"
                        placeholder="请输入用户名"
                        prefix-icon="icon iconfont icon-icon"
                    />
                </el-form-item>
                <el-form-item  prop="password">
                    <el-input
                        v-model="loginForm.password"
                        placeholder="请输入密码"
                        type="password"
                    >
                       <span slot="prefix">
                           <i class="icon iconfont icon-mima"></i>
                       </span>
                       <span slot="suffix">
                           <i class="icon iconfont icon-icon-test1"></i>
                       </span>
                    </el-input>
                </el-form-item>
                <el-form-item style="text-align: center;">
                    <el-button type="danger" style="width: 45%" @click="login">登录</el-button>
                    <el-button type="danger" style="width: 45%" @click="reset">重置</el-button>
                </el-form-item>
            </el-form>
        </el-card>
        <img src="./../../assets/images/login_center_bg.png" alt="" class="login-bg-img">
    </div>
</template>

<script>
    import {checkLogin} from './../../api/adminApi'
    import config from './../../config/config'
    import md5 from 'blueimp-md5'
    export default {
        name: "index",
        data(){
            return {
                // 登录表单的数据绑定对象
                loginForm: {
                    username: '',
                    password: ''
                },
                // 登录表单的验证规则对象
                loginFormRules: {
                    username: [
                        { required: true, message: '请输入登录的用户名', trigger: 'blur' },
                        { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '请输入登录的密码', trigger: 'blur' },
                        { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
                    ],
                }
            }
        },
        methods: {
            login(){
                this.$refs.loginForm.validate(async (valid) => {
                   if(!valid) return;
                   // 去登陆
                   const result = await checkLogin(this.loginForm.username, md5(this.loginForm.password, config.KEY));
                    console.log(result);
                    // 判断
                   if(result.status !==1) return this.$message.error(result.msg);
                   // 登录成功
                    this.$message.success(result.msg);
                    // 保存token
                    window.sessionStorage.setItem(config.MANAGER_TOKEN, result.data.token);
                    window.sessionStorage.setItem(config.MANAGER_USER, JSON.stringify(result.data));
                    // 路由跳转
                    this.$router.push('/home');
                });
            },
            reset(){
                this.$refs.loginForm.resetFields(this.loginForm.username);
            }
        }
    }
</script>

<style scoped lang="less">
   .box-card{
       position: absolute;
       left: 0;
       right: 0;
       margin: 140px auto;
       width: 360px;
       border-top: 10px solid #E9232C;
   }

    .login-bg-img{
        background: #E9232C;
        width: auto;
        height: auto;
        max-width: 100%;
        max-height: 100%;
        margin-top: 205px;
    }
</style>
